import React from 'react'
import { Box, Button, Hidden, withStyles, withWidth } from '@material-ui/core'

const styles = theme => ({
  button: {
    color: '#ffffff',
    flexGrow: 1,
    display: 'inline-flex',
    justifyContent: 'center',
    alignItems: 'center',
    textAlign: 'center',
    flexDirection: 'row',
    padding: theme.spacing(1),
    [theme.breakpoints.down('md')]: {
      fontSize: theme.spacing(1.5),
    },
    [theme.breakpoints.down('xs')]: {
      display: 'block',
      width: '100%',
      justifyContent: 'right',
      alignItems: 'right',
      textAlign: 'right',
      padding: theme.spacing(0.5),
    },
  },
})

const Navigation = ({ classes, sections, callback }) => {
  return (
    <Box>
      {sections.map(section => {
        const haystack = ['case-studies']

        if (haystack.indexOf(section.id) >= 0) {
          return (
            <Hidden xsDown>
              <Button
                key={section.id}
                className={classes.button}
                onClick={() => callback(section.id)}
              >
                {section.title}
              </Button>
            </Hidden>
          )
        }

        return (
          <Button
            key={section.id}
            className={classes.button}
            onClick={() => callback(section.id)}
          >
            {section.title}
          </Button>
        )
      })}
    </Box>
  )
}

export default withWidth()(withStyles(styles)(Navigation))
